@keyframes btn-v2-bg {
  25% {
    background-position: 0% 0%; }
  50% {
    background-position: 100% 0%; }
  75% {
    background-position: 100% 100%; }
  100% {
    background-position: 0% 100%; } }

.btn-v2 {
  margin: 0;
  padding: 0;
  appearance: none;
  display: block;
  position: relative;
  z-index: 1;
  cursor: pointer; }
  .btn-v2::before {
    z-index: 1;
    content: '';
    display: block;
    position: absolute;
    left: 0;
    bottom: -5px;
    height: 50%;
    width: 100%;
    background-color: #6db44a;
    border-radius: 20px;
    transition-duration: .2s; }
  .btn-v2 .btn-v2__inner {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    z-index: 2;
    min-height: 83px;
    top: 0;
    transition-duration: .2s; }
  .btn-v2__bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    box-shadow: inset 0 0 9px rgba(255, 255, 255, 0.13);
    border-radius: 10px;
    background-color: #ff0000;
    background-image: linear-gradient(54deg, rgba(84, 175, 62, 0.97) 0%, rgba(144, 236, 47, 0.97) 100%);
    background-size: 200% 600%;
    background-repeat: no-repeat;
    background-position: 0% 100%;
    transition-duration: .5s;
    animation: btn-v2-bg 5s linear infinite; }
  .btn-v2__text {
    display: block;
    position: relative;
    z-index: 3;
    text-shadow: 0 2px 0 rgba(110, 185, 71, 0.35);
    color: #ffffff;
    font-size: 14px;
    font-weight: 500;
    line-height: 25.23px;
    text-transform: uppercase;
    letter-spacing: 1.07px;
    border-radius: 10px;
    transition-duration: .2s; }
  .btn-v2:hover .btn-v2__inner {
    top: 5px; }
  .btn-v2:hover::before {
    border-radius: 10px; }
  .btn-v2:hover .btn-v2__bg {
    background-size: 100% 100% !important;
    background-position: center center; }

